.fl-project-editor .fl-annotation-list {
	list-style-type: none;
	margin: 0;
	padding: 1rem;
	border-bottom: 1px solid #eee;

	&:empty {
		padding: 0;
	}
}

.fl-annotation-list-wrap {
	background: #fff;
	margin: 0;
	font-size: 0.8rem;
	max-height: 10rem;
	overflow: auto;
	position: relative;

	box-shadow: 0 -10px 10px -10px rgba(0, 0, 0, 0.25) inset;
	transition: box-shadow 0.5s;
	&.scroll-end {
		box-shadow: 0 -10px 10px -10px rgba(0, 0, 0, 0) inset;
	}
}

.fl-project-editor .fl-annotation-list > li {
	display: flex;
	position: relative;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	list-style-type: none;
	line-height: 1.35rem;
	color: #b1b1b1;
	cursor: pointer;
}

.fl-project-editor .fl-annotation-list .drop-overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 10;
	flex-direction: column;
	display: none;
}

.fl-project-editor .fl-annotation-list.dragging .drop-overlay {
	display: flex;
}

.fl-project-editor .fl-annotation-list .drop-above,
.fl-project-editor .fl-annotation-list .drop-below {
	flex-grow: 1;
}

.fl-project-editor .fl-annotation-list .drop-above {
	border-top: 1px solid #fff;
}

.fl-project-editor .fl-annotation-list .drop-below {
	border-bottom: 1px solid #fff;
}

.fl-project-editor .fl-annotation-list .drop-above.dropping {
	border-top: 1px solid #666;
}

.fl-project-editor .fl-annotation-list .drop-below.dropping {
	border-bottom: 1px solid #666;
}

.fl-project-editor .fl-annotation-list > li .text {
	display: block;
	overflow: hidden;
	text-overflow: ellipsis;
	flex-grow: 1;
	white-space: nowrap;
}

.fl-project-editor .fl-annotation-list > li .icon {
	display: none;
	margin-left: 0.5rem;
	color: #333333;
	opacity: 0;
	flex-shrink: 0;
}
.fl-project-editor .fl-annotation-list > li.selected .icon,
.fl-project-editor .fl-annotation-list > li:hover .icon {
	display: inline-block;
	margin-left: 0.5rem;
	opacity: 0.5;
}
.fl-project-editor .fl-annotation-list > li .icon:hover {
	opacity: 1;
}

.fl-project-editor .fl-annotation-list > li:hover {
	color: #000;
}

.fl-project-editor .fl-annotation-list > li::before {
	content: "";
	width: 0.75rem;
	height: 0.75rem;
	display: inline-block;
	background: #b1b1b1;
	vertical-align: middle;
	margin-right: 0.5rem;
	border-radius: 100%;
	flex-shrink: 0;
}

.fl-project-editor .fl-annotation-list > li.selected {
	cursor: default;
	&::before {
		background: #333333;
	}
	.text {
		color: #333;
	}
}

.fl-annotation-list li.drag-over {
	background: #f00;
}

