.fl-project-editor .side-panel .help-tip {
	display: inline-block;
	position: relative;
	width: 0.7rem;
	height: 0.7rem;
	transform: translate(0, 1px);
	opacity: 0.6;
	background-image: url(data:image/svg+xml;base64,);
}

.fl-project-editor .side-panel .help-tip:hover {
	opacity: 1;
}

.fl-project-editor .side-panel .help-tip:after {
	content: attr(data-popup-body);
	position: absolute;
	bottom: 100%;
	left: 50%;
	transform: translate(-50%, 0);
}

.fl-annotations-tip {
	font-family: "Canva Sans Variable", sans-serif;
	max-width: 15rem;
	background: #fff;
	color: #333;
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
	border-radius: 0.5rem;
	padding: 0.625rem;
	font-size: 0.72rem;
	line-height: 1.25em;
	position: absolute;
	z-index: 999999;
	pointer-events: none;
}

.fl-annotations-tip:after {
	content: "";
	position: absolute;
	bottom: -8px;
	left: 50%;
	margin-left: -8px;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 8px 8px 0 8px;
	border-color: #fff transparent transparent transparent;
}

.fl-annotations-tip.hidden {
	display: none;
}
