body.fl-cursor-custom,
body.fl-cursor-custom * {
	cursor: none !important;
}

body.fl-cursor-pointer,
body.fl-cursor-pointer * {
	cursor: pointer !important;
}

body.fl-cursor-default .fl-annotations .fl-annotations-cursor,
body.fl-cursor-pointer .fl-annotations .fl-annotations-cursor {
	visibility: hidden;
}

.fl-annotations {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(255, 255, 255, 0);
	cursor: pointer;
	pointer-events: none;
	user-select: none !important;
	transition: opacity 0.25s;
}

.fl-annotations.is-dragging,
.fl-annotations.is-dragging * {
	pointer-events: none;
}

.flourish-popup {
	z-index: 100000;
}
